// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from "vue";
import App from "./App";
import router from "./router";
import ElementUI from "element-ui";
import http from "@/utils/http";
import api from "@/utils/api";
import axios from "axios";
Vue.prototype.$http = http;
Vue.prototype.$api = api;

Vue.use(ElementUI);
Vue.config.productionTip = false;

new Vue({
  el: "#app",
  data() {
    return {
      async GetConfigJSON() {
        let configJson = JSON.parse(window.localStorage.getItem(this.SessionPrefix + "ConfigJSON"));
        if (configJson)
          return configJson;

        let res = await axios
          .get("/static/config.json")
          // .then(result => {
          //   configJson = result.data;
          // })
          .catch(error => {
            console.log(error);
          });

        configJson = res.data;
        window.localStorage.setItem(api.SessionPrefix + "ConfigJSON", JSON.stringify(configJson));

        return configJson;
      },
      async GetSrvConfig() {
        let configInfo = JSON.parse(window.localStorage.getItem(this.SessionPrefix + "ConfigInfo"));
        if (configInfo)
          return configInfo;

        var baseURL = await api.GetBaseURL();

        let res = await axios
          .get(baseURL.concat(api.getConfigInfo))
          // .then(result => {
          //   configJson = result.data;
          // })
          .catch(error => {
            console.log(error);
          });

        configInfo = res.data.Datas;
        window.localStorage.setItem(api.SessionPrefix + "ConfigInfo", JSON.stringify(configInfo));
        
        return configInfo;
      },
    }
  },
  router,
  http,
  api,
  components: {
    App
  },
  template: "<App/>"
});



