// import studentsDetail from "../pages/person/teacher/classMana/studentsDetail.vue";
const studentsDetail = () =>
  import("../pages/person/teacher/classMana/studentsDetail.vue");
// import statistics from "../pages/person/teacher/statistics.vue";
const statistics = () => import("../pages/person/teacher/statistics.vue");

// 评价
// import teacherEvaluate from "../pages/person/teacher/evaluate/teacherEvaluate.vue";
const teacherEvaluate = () =>
  import("../pages/person/teacher/evaluate/teacherEvaluate.vue");
// import teacherEvaluateList from "../pages/person/teacher/evaluate/teacherEvaluateList.vue";
const teacherEvaluateList = () =>
  import("../pages/person/teacher/evaluate/teacherEvaluateList.vue");
