// import teacherPrepare from "../pages/person/teacher/prepare/prepare.vue";
const teacherPrepare = () =>
  import("../pages/person/teacher/prepare/prepare.vue");
// import prepareView from "../pages/person/teacher/prepare/prepareView.vue";
