const personalInfo = () =>
  import("../pages/person/teacher/setInfo/personalInfo.vue");
// import teachingInfo from "../pages/person/teacher/setInfo/teachingInfo.vue";
const teachingInfo = () =>
  import("../pages/person/teacher/setInfo/teachingInfo.vue");
// import uploadImage from "../pages/person/teacher/setInfo/uploadImage.vue";
const uploadImage = () =>
  import("../pages/person/teacher/setInfo/uploadImage.vue");
// import contactInfo from "../pages/person/teacher/setInfo/contactInfo";
const contactInfo = () =>
  import("../pages/person/teacher/setInfo/contactInfo.vue");
// import confidentialInfo from "../pages/person/teacher/setInfo/confidentialInfo";
const confidentialInfo = () =>
  import("../pages/person/teacher/setInfo/confidentialInfo.vue");
const changePwd = () => import("../pages/person/teacher/setInfo/changePwd.vue");
//老师消息通知
// import allMes from "../pages/person/teacher/mesNotify/allMes.vue";
const allMes = () => import("../pages/person/teacher/mesNotify/allMes.vue");
// import unreadMes from "../pages/person/teacher/mesNotify/unreadMes.vue";
const unreadMes = () =>
  import("../pages/person/teacher/mesNotify/unreadMes.vue");
// import readedMes from "../pages/person/teacher/mesNotify/readedMes.vue";
const readedMes = () =>
  import("../pages/person/teacher/mesNotify/readedMes.vue");

// 大纲
// import outline from "../pages/person/teacher/outline/ouline.vue";
const outline = () => import("../pages/person/teacher/outline/ouline.vue");
// import createProgram from "../pages/person/teacher/outline/createProgram.vue";
const createProgram = () =>
  import("../pages/person/teacher/outline/createProgram.vue");
// import createContent from "../pages/person/teacher/outline/createContent.vue";
const createContent = () =>
  import("../pages/person/teacher/outline/createContent.vue");
// import programView from "../pages/person/teacher/outline/programView.vue";
const programView = () =>
  import("../pages/person/teacher/outline/programView.vue");
// import contentView from "../pages/person/teacher/outline/contentView.vue";
const contentView = () =>
  import("../pages/person/teacher/outline/contentView.vue");

// 学生空间
// import student from "../pages/person/person.vue";
const student = () => import("../pages/person/person.vue");
// import studentView from "../pages/person/student/view.vue";
const studentView = () => import("../pages/person/student/view.vue");
// import studentCommonDetail from "../pages/person/student/commonDetail.vue";
const studentCommonDetail = () =>
  import("../pages/person/student/commonDetail.vue");
// import studentMembers from "../pages/person/student/members.vue";
const studentMembers = () => import("../pages/person/student/members.vue");
// import studentNotes from "../pages/person/student/notes.vue";
const studentNotes = () => import("../pages/person/student/notes.vue");
// import studentGradeView from "../pages/person/student/grade.vue";
const studentGradeView = () => import("../pages/person/student/grade.vue");
// import studentActives from "../pages/person/student/actives.vue";
const studentActives = () => import("../pages/person/student/actives.vue");
// import studentCurriculum from "../pages/person/student/curriculum.vue";
// const studentCurriculum = () =>
// import("../pages/person/student/curriculum.vue");
const studentCourseList = () =>
  import("../pages/person/student/course/courseList.vue");
const studentClassRecord = () =>
  import("../pages/person/student/course/classRecord/classRecord.vue");
// import studentCollect from "../pages/person/student/collect.vue";
const studentCollect = () => import("../pages/person/student/collect.vue");
// import studentTalk from "../pages/person/student/talk.vue";
const studentTalk = () => import("../pages/person/student/talk.vue");
// import studentTaskList from "../pages/person/student/task/list.vue";
const studentTaskList = () => import("../pages/person/student/task/list.vue");
// import studentTaskDetail from "../pages/person/student/task/detail.vue";
const studentTaskDetail = () =>
  import("../pages/person/student/task/detail.vue");
// import studentBank from "../pages/person/student/bank.vue";
const studentBank = () => import("../pages/person/student/bank.vue");
// import studentReview from "../pages/person/student/review.vue";
const studentReview = () => import("../pages/person/student/review.vue");

// 教师空间
const teacher = () => import("../pages/person/person.vue");

Vue.use(Router);
const routes = [
  {
    path: "/",
    redirect: "/main"
  },
  {
    path: "/main",
    component: main,
    redirect: "/main/index",
    children: [
      {
        path: "binding/:id",
        component: binding
      },
      {
        path: "login",
        component: login
      },
      {
        path: "index",
        name: "index",
        component: indexPage
      },
      {
        path: "course",
        component: transitionPage,
        redirect: "course/page",
        children: [
          {
            path: "page",
            name: "course",
            component: coursePage
          }
        ]
      },
      {
        name: "interactive",
        path: "interactive",
        component: interactive,
        redirect: "interactive/page",
        children: [
          {
            path: "page",
            component: interactivePage
          },
          {
            path: "detail",
            component: interactiveDetail
          }
        ]
      },
      {
        path: "detail",
        name: "CourseDetail",
        component: courseDetail
      },
      {
        path: "live",
        name: "live",
        component: livePage
      },
      {
        path: "resource",
        component: transitionPage,
        redirect: "resource/page",
        children: [
          {
            path: "page",
            component: resourcePage
          },
          {
            path: "detail",
            component: resourceDetail
          }
        ]
      },
      {
        path: "field",
        component: fieldPage
      },
      {
        path: "teacher",
        component: teacherPage
      },
      {
        path: "mesNotify",
        component: teacher,
        redirect: "mesNotify/allMes",
        children: [
          {
            path: "allMes",
            component: allMes
          },
          {
            path: "unreadMes",
            component: unreadMes
          },
          {
            path: "readedMes",
            component: readedMes
          }
        ]
      },
      {
        //个人设置页面
        path: "setInfo",
        component: teacher,
        redirect: "setInfo/basicInfo",
        children: [
          {
            path: "basicInfo",
            component: basicInfo
          },
          {
            path: "personalInfo",
            component: personalInfo
          },
          {
            path: "teachingInfo",
            component: teachingInfo
          },
          {
            path: "uploadImage",
            component: uploadImage
          },
          {
            path: "contactInfo",
            component: contactInfo
          },
          {
            path: "confidentialInfo",
            component: confidentialInfo
          },
          {
            path: "changePwd",
            component: changePwd
          }
        ]
      },
      {
        path: "teacherSpace",
        component: teacher,
        redirect: "teacherSpace/teacherEvaluate",
        children: [
          {
            path: "teacherView/:id",
            component: () => import("../pages/person/teacher/teacherView.vue")
          },
          {
            path: "teacherView/:id",
            component: () => import("../pages/person/teacher/teacherView.vue")
          },
          {
            path: "prepare",
            component: teacherPrepare,
            redirect: "prepare/courseView",
            children: [
              {
                path: "view",
                component: prepareView
              },
              {
                path: "prepareAll",
                component: prepareAll
              },
              {
                name: "prepareForms",
                path: "prepareForms",
                component: prepareForms
              },
              {
                name: "courseView",
                path: "courseView",
                component: () =>
                  import("../pages/person/teacher/myCourse/course/courseList.vue")
              },
              {
                name: "createCourse",
                path: "createCourse/:courseId?/:classId?/:courseName?",
                component: () =>
                  import("../pages/person/teacher/myCourse/course/courseEdit.vue")
              },
              {
                name: "classTemplate",
                path: "classTemplate",
                component: () =>
                  import("../pages/person/teacher/myCourse/courseTemplate/templateList.vue")
              },
              {
                name: "newCsTemp",
                path: "newCsTemp",
                component: () =>
                  import("../pages/person/teacher/myCourse/courseTemplate/templateEdit.vue")
              },
              {
                name: "lessonInfo",
                path: "lessonInfo",
                component: () =>
                  import("../pages/person/teacher/myCourse/lessonInfo.vue")
              },
              {
                name: "classRecord",
                path: "classRecord",
                component: () =>
                  import("../pages/person/teacher/myCourse/classRecord/classRecord.vue")
              },
              {
                name: "studentTask",
                path: "studentTask",
                component: () =>
                  import("../pages/person/teacher/myCourse/courseTeaching/StudentTask.vue")
              }
            ]
          },
          {
            path: "practiceBank",
            component: teacherPrepare,
            redirect: "practiceBank/index",
            children: [
              {
                name: "practiceBankIndex",
                path: "index",
                component: () =>
                  import("../pages/person/teacher/practiceBank/index.vue")
              }
            ]
          },
          {
            path: "outline",
            component: outline,
            redirect: "outline/programView",
            children: [
              {
                name: "createProgram",
                path: "createProgram",
                component: createProgram
              },
              {
                name: "createContent",
                path: "createContent",
                component: createContent
              },
              {
                name: "programView",
                path: "programView",
                component: programView
              },
              {
                name: "contentView",
                path: "contentView",
                component: contentView
              }
            ]
          },
          {
            path: "teacherResource",
            component: teacherResource
          },
          {
            path: "teacherSearch",
            component: teacherSearch
          },
          {
            path: "teacherEvaluate",
            component: teacherEvaluate,
            redirect: "teacherEvaluate/list",
            children: [
              {
                path: "list",
                component: teacherEvaluateList
              },
              {
                path: "Calendar",
                component: teacherEvaluateCalendar
              },
              {
                name: "detail",
                path: "detail",
                component: teacherEvaluateDetail
              },
              {
                name: "evaluateddetail",
                path: "evaluateddetail",
                component: teacherEvaluatedDetail
              }
            ]
          },
          {
            path: "person",
            component: person
          },
          {
            path: "activites",
            component: activites
          },
          {
            path: "classMana",
            component: classMana,
            redirect: "classMana/list",
            children: [
              {
                path: "list",
                component: classList
              },
              {
                path: "view/:id",
                component: classView
              },
              {
                path: "score/:id",
                component: score
              },
              {
                path: "signIn/:id",
                component: signIn
              },
              {
                path: "students/:id",
                component: students
              },
              {
                path: "student/:stuID",
                component: studentsDetail
              }
            ]
          },
          {
            path: "statistics",
            component: statistics
          }
        ]
      },
      {
        path: "studentSpace",
        component: student,
        redirect: "studentSpace/view",
        children: [
          {
            path: "view",
            component: studentView
          },
          {
            path: "member",
            component: studentMembers
          },
          {
            path: "notes",
            component: studentNotes
          },
          {
            path: "task",
            component: transitionPage,
            redirect: "task/list",
            children: [
              {
                path: "list",
                component: studentTaskList
              },
              {
                path: "detail/:id",
                component: studentTaskDetail
              }
            ]
          },
          {
            path: "grade",
            component: transitionPage,
            redirect: "grade/view",
            children: [
              {
                path: "view",
                component: studentGradeView
              },
              {
                path: "detail/:id",
                component: studentCommonDetail
              }
            ]
          },
          {
            path: "activities",
            component: transitionPage,
            redirect: "activities/view",
            children: [
              {
                path: "view",
                component: studentActives
              },
              {
                path: "detail/:id",
                component: studentCommonDetail
              }
            ]
          },
          {
            path: "lessons",
            component: studentCourseList
          },
          {
            path: "classRecord",
            component: studentClassRecord
          },
          {
            path: "collect",
            component: studentCollect
          },
          {
            path: "talk",
            component: studentTalk
          },
          {
            path: "bank",
            component: studentBank
          },
          {
            path: "review",
            component: studentReview
          }
        ]
      }
    ]
  }
];
export default new Router({
  routes
});



