<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import "element-ui/lib/theme-chalk/index.css";
import axios from "axios";
import api from "./utils/api";
export default {
  name: "App",
  async beforeCreate() {
    await this.$root.GetConfigJSON();
  },
  mounted() {
    axios
      .get("/static/config.json")
      .then(result => {
        // 以下内容不可修改
        window.localStorage.setItem("campus_ip", result.data.Host);
        window.localStorage.setItem("campus_AMSip", result.data.AMSHost);
        window.localStorage.setItem("campus_port", result.data.Port);
        window.localStorage.setItem("campus_RECip", result.data.RECHost);
        window.localStorage.setItem("campus_RECport", result.data.RecPort);
        window.localStorage.setItem(
          "campus_webSocketPort",
          result.data.webSocketPort
        );
        window.localStorage.setItem("campus_H5port", result.data.H5Port);

        let configJson = result.data;
        window.localStorage.setItem(
          api.SessionPrefix + "ConfigJSON",
          JSON.stringify(configJson)
        );
      })
      .catch(error => {
        console.log(error);
      });
  }
};
</script>

<style>
@import url("./assets/fonts/iconfont/iconfont.css");
@import url("./assets/fonts/STxk/STxingkai.css");
* {
  box-sizing: border-box;
  text-decoration: none;
  padding: 0;
  margin: 0;
}
html,
body {
  height: 100%;
}
body {
  min-width: 1200px;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* background: rgb(248, 248, 248); */
}
.wrap {
  width: 100%;
  background: #f8f8f8;
}
