}
.blue-bg {
  background: #3fabff !important;
}
.grown-bg {
  background: #eeb00a !important;
}
.violet-bg {
  background: violet !important;
}
button {
  outline: none !important;
}
.img-box img {
  width: 100%;
  height: 100%;
}
/* 覆盖 */
.el-table td,
.el-table th {
  padding: 8px 0 !important;
}
.more {
  float: right;
  color: #aaa;
  font-size: 0.8rem;
  cursor: pointer;
}
.empty-tip {
  width: 100%;
