"use strict";

import axios from "axios";
import api from "./api";
import cookie from "./cookieOperate";
// const baseURL = api.base;
const timeout = 30000;
axios.interceptors.request.use(
    config => {
        // loading
        return config;
    },
    error => {
        return Promise.reject(error);
    }
);

axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        return Promise.resolve(error.response);
    }
);

function checkStatus(response) {
    if (response && response.status === 200) {
        let res = response.data;
        if (res.Result || res.ModuleOperatings) {
            return res;
        } else {
            if (!res.Datas && !res.Info) {
                res.Info = "没有数据";
            }
            return {
                message: res.Info,
                type: "warning"
            };
        }
    } else {
        return {
            message: "网络连接超时，请重新尝试！",
            type: "error"
        };
    }
}
export default {
    pageSize: 12,
    backToLogin() {
        window.sessionStorage.setItem("last_path", window.location.href);
        setTimeout(() => {
            window.location.href =
                window.location.href.split("/")[0] + "/#/main/login/?relogin=1";
            window.location.reload();
        }, 1520);
    },
    async post(url, data) {
        var baseURL = await api.GetBaseURL();
        return axios({
            method: "post",
            baseURL: baseURL,
            timeout: timeout,
            url,
            async: false,
            data: data,
            headers: {
                "X-Requested-With": "XMLHttpRequest",
                "Content-Type": "application/json; charset=UTF-8"
            }
        }).then(response => {
            return checkStatus(response);
        });
    },
    async postFormData(url, data) {
        var baseURL = await api.GetBaseURL();
        return axios({
            method: "post",
            baseURL: baseURL,
            timeout: timeout,
            url,
            async: false,
            data: data,
            headers: {
                "X-Requested-With": "XMLHttpRequest",
                "Content-Type": "multipart/form-data"
            }
        }).then(response => {
            return checkStatus(response);
        });
    },
    async postFormDataAndProgress(url, data, event) {
      var baseURL = await api.GetBaseURL();
      return axios({
        method: "post",
        baseURL: baseURL,
        timeout: timeout,
        url,
        async: false,
        data: data,
        onUploadProgress: event.onUploadProgress,
        headers: {
          "X-Requested-With": "XMLHttpRequest",
          "Content-Type": "multipart/form-data"
        }
      }).then(response => {
        return checkStatus(response);
      });
    },
    async get(url, params) {
        var baseURL = await api.GetBaseURL();
        return axios({
            method: "get",
            baseURL: baseURL,
            timeout: timeout,
            url,
            params // get 请求时带的参数
        }).then(response => {
            return checkStatus(response);
        });
    },
    // 获取用户token
    getToken() {
        let temp = api.cookie.token;
        if (cookie.get(temp)) {
            let token = cookie.get(temp);
            return token;
        } else {
            //return "H4sIAKnpoVwA/2NgZGBg+A8EIBoE2EAMBQP8wJCbYZgAAGj2u0cAAQAA";
            this.backToLogin();
        }
    },
    // 用户信息
    getUser() {
        if (cookie.get(api.cookie.user_infor)) {
            let userInfo = cookie.get(api.cookie.user_infor);
            if (userInfo != null && userInfo != "") {
                let obj = JSON.parse(userInfo);
                return obj;
            } else {
                //this.backToLogin();
            }
        } else {
            //this.backToLogin();
        }
    },
    // 老师信息
    getThc() {
        if (cookie.get(api.cookie.thc_infor)) {
            let thcInfo = cookie.get(api.cookie.thc_infor);
            if (
                thcInfo != undefined &&
                thcInfo != null &&
                thcInfo != "undefined" &&
                thcInfo != ""
            ) {
                let obj = JSON.parse(thcInfo);
                return obj;
            } else {
                return null;
            }
        } else {
            return null;
        }
    },
    // 学生信息
    getStu() {
        if (cookie.get(api.cookie.stu_infor)) {
            let stuInfo = cookie.get(api.cookie.stu_infor);
            if (
                stuInfo != undefined &&
                stuInfo != null &&
                stuInfo != "undefined" &&
                stuInfo != ""
            ) {
                let obj = JSON.parse(stuInfo);
                return obj;
            } else {
                return null;
            }
        } else {
            return null;
        }
    },
    // 视频配置
    getConfig() {
        if (cookie.get(api.cookie.video_config)) {
            return JSON.parse(cookie.get(api.cookie.video_config));
        } else {
            this.backToLogin();
        }
    },
    logined() {
        return cookie.get(api.cookie.token) ? true : false;
    }
};



